
.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size:  24px;
    font-weight: bold;
    /* background-color: #CCC */;
}
.Icon-check {
    position: absolute;
    left: 12px;
}
.Icon-check--active {
    color: #4caf50;
}

.Icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
}


.Icon-delete:hover svg{
    fill: red;
}
.Icon-check:hover svg{
    fill: green;
}